import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
//  import Carousel from 'react-material-ui-carousel';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
//  import { ProfileAvatar } from 'components/profile';
import ActionButton from 'components/ActionButton';
import MemoryReviewCarousel from 'components/MemoryReviewCarousel';
import OwnerInfo from 'components/OwnerInfo';
import commonUtils from 'utils';

//  import ReviewItem from 'components/ReviewItem';
import api from 'pages/Api';

async function getReviewMemories(
  handleMemoryCallback,
  handleTokenCallback,
  nextToken = '',
) {
  try {
    const resp = await api.getPendingMemories(nextToken);
    if (resp) {
      if (resp.memories) {
        /* eslint-disable no-console */
        console.log('review memories', resp);
        const thumbnails = await Promise.all(
          api.getPendingThumbnails(resp.memories),
        );
        handleMemoryCallback(thumbnails);
        handleTokenCallback(resp.next_continuation_token || '');
      }
    }
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

async function acceptMemory(memoryID, handleAcceptCallback) {
  try {
    const userInfo = commonUtils.getUserInfo();
    const resp = await api.acceptShare(memoryID, userInfo.email);
    console.log(resp);
    handleAcceptCallback(resp);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

async function rejectMemory(memoryID, handleRejectCallback) {
  try {
    const userInfo = commonUtils.getUserInfo();
    const resp = await api.rejectShare(memoryID, userInfo.email);
    console.log(resp);
    handleRejectCallback(resp);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

export default function ReviewMemories({ memoryID }) {
  const [picIndex, setPicIndex] = useState(0);
  const [nextToken, setNextToken] = useState('');
  const [pendingMemories, setPendingMemories] = useState([]);
  console.log(memoryID);

  const handleTokenChange = (newToken) => setNextToken(newToken);

  const addPendingMemories = (details) => { setPendingMemories((arr) => arr.concat(details)); };

  useEffect(() => {
    getReviewMemories(addPendingMemories, handleTokenChange, nextToken);
  }, []);

  const handleAcceptActionResults = () => {};
  const handleRejectActionResults = () => {};

  const handleAccept = () => {
    const memID = pendingMemories[picIndex].thumbnail.memory_id;
    acceptMemory(memID, handleAcceptActionResults);
    console.log('accepting memoryID', memID);
  };

  const handleReject = () => {
    const memID = pendingMemories[picIndex].thumbnail.memory_id;
    rejectMemory(memID, handleRejectActionResults);
    console.log('rejecting memoryID', memID);
  };

  /* eslint-disable no-console */
  console.log('pending memories', pendingMemories);
  return (pendingMemories.length > 0) ? (
    <Box
      sx={{
        display: 'flex',
        width: '100%',
        backgroundColor: 'transparent',
        backgroundSize: 'cover',
        backgroundImage: 'linear-gradient(white, #0e516a)',
        justifyContent: 'center',
      }}
    >
      <Stack
        direction="column"
        alignItems="center"
        sx={{ width: '100%', my: 3 }}
      >
        <OwnerInfo
          name={pendingMemories[picIndex].owner_details.name}
          email={pendingMemories[picIndex].owner_details.email}
          picture={pendingMemories[picIndex].owner_details.picture}
        />
        <Typography sx={{ mb: 2 }}>has shared a memory with you</Typography>
        <MemoryReviewCarousel memories={pendingMemories} handleIndexChange={setPicIndex} />
        <Stack direction="row" sx={{ m: 1 }}>
          <ActionButton label="Accept" color="primary" handleClick={handleAccept} />
          <ActionButton label="Reject" color="error" handleClick={handleReject} />
        </Stack>
      </Stack>
    </Box>
  ) : null;
}

ReviewMemories.defaultProps = {
  memoryID: '',
};

ReviewMemories.propTypes = {
  memoryID: PropTypes.string,
};
